import React, {Component} from 'react'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card'
import CardActions from '@material-ui/core/CardActions'
import CardHeader from '@material-ui/core/CardHeader'
import Typography from '@material-ui/core/Typography'

import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button'

import form_data from '../data'
import styles from './styles'

class InputForm extends Component {
    constructor(props){
        super(props)
        
        this.state = {
            upload: '',
            customUpload: false,
            network: '',
            goldStandard: '',
            top: 100
        }

    }

    checkParameters = () => {
        this.props.handleRun(this.state)
    }

    handleChange = name => event => {
        if (name === 'customUpload'){
            if (event.target.files.length === 1){
                console.log(event.target.files[0])
                this.setState({customUpload: event.target.files[0]})
            }
            return;
        }
        this.setState({ [name]: event.target.value });
    };

    render(){
        const { classes } = this.props;
        const { upload, 
            customUpload, 
            network, 
            goldStandard, 
            top
        } = this.state;

        return (
            <div>
                <Card className={classes.card}>
                    <CardHeader
                        title={form_data.title}
                        subheader={form_data.subheader} 
                    />
                    <FormControl className={classes.formControl}>
                        <InputLabel htmlFor="upload-dropdown" shrink>Upload</InputLabel>
                        <Select
                            required
                            native
                            value={this.state.upload}
                            onChange={this.handleChange('upload')}
                            inputProps={{
                                name: 'upload',
                                id: 'upload-dropdown',
                            }}
                        >
                            <option value="sampleA">Sample A</option>
                            <option value="sampleB">Sample B</option>
                            <option value="custom">Custom</option>
                        </Select>
                        <FormHelperText>{form_data.uploadHelp}</FormHelperText>
                    </FormControl>
                    {upload === 'custom' &&
                        <FormControl className={classes.formControl}>
                            <input
                                accept="text/*"
                                className={classes.input}
                                id="custom-upload"
                                type="file"
                                onChange={this.handleChange('customUpload')}
                            />
                            <label htmlFor="custom-upload">
                                <Button variant="contained" component="span" className={classes.button}>
                                    {form_data.uploadButtonText}
                                </Button>
                                {customUpload && <Typography component='p'>
                                    {customUpload.name} {customUpload.size}b
                                </Typography>}
                            </label>
                        </FormControl>
                    }
                    <FormControl className={classes.formControl}>
                        <InputLabel htmlFor="network-entry"></InputLabel>
                        <TextField
                            required
                            id="network-entry"
                            label={form_data.networkText}
                            className={classes.textField}
                            value={network}
                            onChange={this.handleChange('network')}
                            margin="normal"
                        />
                    </FormControl>
                    <FormControl className={classes.formControl}>
                        <InputLabel htmlFor="gold-standard"></InputLabel>
                        <TextField
                            id="gold-standard"
                            label={form_data.goldStandardText}
                            className={classes.textField}
                            value={goldStandard}
                            onChange={this.handleChange('goldStandard')}
                            margin="normal"
                        />
                        <FormHelperText>{form_data.goldStandardHelp}</FormHelperText>
                    </FormControl>
                    <FormControl className={classes.formControl}>
                        <InputLabel htmlFor="top"></InputLabel>
                        <TextField
                            id="top"
                            label={form_data.topText}
                            className={classes.textField}
                            value={top}
                            onChange={this.handleChange('top')}
                            margin="normal"
                        />
                        <FormHelperText>{form_data.topHelp}</FormHelperText>
                    </FormControl>
                    <CardActions>
                        <Button 
                            variant="contained" 
                            className={classes.button} 
                            onClick={this.checkParameters}>
                            {form_data.runText}
                        </Button>
                    </CardActions>
                </Card>
            </div>
        )
    }
}
InputForm.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(InputForm);